<template>
  <div class='dex'>
    <nprogress :nprogressEvent='npLength'></nprogress>
    <div class='header'>
      <div class='header-left'>
        <img src="../assets/28doctorLogo.png" alt="">
        <span>胡大一诊所</span>
      </div>
      <div class='header-right'>
        <div class='header-right-textOne pull-left'>首页</div>
        <div class='header-right-textTwo pull-right'>客服电话：400-688-0788</div>
      </div>
    </div>
    <div class='center'>
      <div class='tu clearfix'>
        <div class='mainLogo pull-left'>
          <img src="../assets/26mainLogo.png" alt="">
        </div>
        <div class='menu pull-right'>
          <h2>登录</h2>
          <div class='logn-from'>
            <el-form :model="user" :rules="rules" ref="user" label-width="0px" class="demo-ruleForm">
              <el-form-item prop="username" style='margin-top:40px;'>
                <el-input placeholder="手机号" v-model="user.username"></el-input>
              </el-form-item>
              <el-form-item prop="password">
                <el-input style='margin-top:12px;' type='password' placeholder="请输入密码" v-model="user.password"></el-input>
              </el-form-item>
              <div class='VerificationCode' v-if='VerificationCode'>
                <el-form-item prop="VerificationCode" style='display:inline-block;'>
                  <el-input style='width:178px' v-model='user.VerificationCode' placeholder="验证码"></el-input>
                </el-form-item>
                <a href="javascript:;" @click='getCodeUrl'>
                  <img :src="imgUrl" alt="">
                </a>
              </div>
              <div class='remenberWord clearfix'>
                <div class='remenberWord-L pull-left'>
                  <el-checkbox v-model='checked'></el-checkbox>
                  <span>记住密码</span>
                </div>
                <div @click='changePassword' class='remenberWord-R pull-right'>
                  忘记密码
                </div>
              </div>
              <!--<el-input v-model="user.username" @keyup.enter='logn' type="password" placeholder="密码"></el-input>-->
              <!--<input type="text" class='nameClass' placeholder="手机号" v-model="user.username" >

               <input type="password" class='passWordClass' @keyup.enter='logn' v-model="user.password" placeholder="密码">-->
              <button @click="logn($event)">登录</button>
            </el-form>
          </div>
        </div>
      </div>
    </div>
    <div class='footerOne'>
      <div class='footer-text'>
        <span>关于我们</span>&nbsp;
        <span>@2017天津上线科技有限公司深圳分公司</span>&nbsp;
        <span>粤ICP备17054586号</span>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import addtime from '../untils/im.js'
import publicFn from '../untils/Public.js';
import nprogress from './public/nprogress.vue'
export default {
  data: function() {
    return {
      npLength: 'length0',
      user: {
        grant_type: 'password',
        username: localStorage.getItem('username') || '',
        password: localStorage.getItem('password') || '',
        client_secret: '20170101',
        client_id: 'PC',
        VerificationCode: ''
      },
      rules: {
        username: [
          { required: true, message: '请填写手机', trigger: 'blur' },
          { validator: (rule, value, callback) => { if (/^1[34578]\d{9}$/.test(value) == false) { callback(new Error("请填写正确手机")); } else { callback(); } }, trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' },
        ],
        VerificationCode: [
          { required: true, message: '请输入验证码', trigger: 'blur' },
          { min: 4, max: 4, message: '长度是4个字符', trigger: 'blur' },
        ]
      },
      checked: false,
      count: 0,
      VerificationCode: false,
      imgUrl: '',
      lengthNumber: 0
    }
  },
  created() {
    if (localStorage.getItem('checked')) {
      this.checked = true;
    }
  },
  methods: {
    // 提示框
    success() {
      this.$message({
        message: '登录成功',
        type: 'success'
      });
    },
    warning() {
      this.$message({
        message: '密码错误',
        type: 'warning'
      });
    },
    //輸入號碼驗證
    changeCount(username) {
      var phoneRule = /^1[3,4,5,7,8]\d{9}$/
      if (!phoneRule.test(username)) {

      } else {

      }
    },
    //登陸驗證
    logn(event) {
      event.preventDefault();
      var that = this;
      this.$refs['user'].validate((valid) => {
        if (valid) {
          if (that.VerificationCode) {
            // 菜单存储数据的去除
            localStorage.removeItem('twoIsShow');
            localStorage.removeItem('twoIsShow2');
            localStorage.removeItem('childTree');
            localStorage.removeItem('systemManagement');
            var url = '/api/uaa/oauth/token';
            var user = {
              grant_type: 'password',
              username: that.user.username,
              password: that.user.password,
              client_secret: '20170101',
              client_id: 'PC'
            }
            this.npLength = 'length100'//进度条
            that.$http.post(url, user, { emulateJSON: true }).then(function(Res) {
              // 验证码
              var url = '/api/uaa/eztUsers/checkCode';
              this.$http.get(url, {
                params: {
                  Code: that.user.VerificationCode
                }
              }).then(function(res) {
                if (res.body) {
                  // 记住密码
                  if (that.checked) {
                    localStorage.setItem('username', that.user.username);
                    localStorage.setItem('password', that.user.password);
                    localStorage.setItem('checked', '11')
                  }
                  if (!that.checked) {
                    localStorage.removeItem('username');
                    localStorage.removeItem('password');
                    localStorage.removeItem('checked')
                  }
                  // 把這個存起來，用於判斷用戶是否登陸
                  localStorage.setItem('access_token', Res.body.access_token);
                  // sessionStorage.setItem('access_token', Res.body.access_token);
                  // 限制
                  var obj = {
                    nowTime: new Date().getTime(),
                    timeLimit: 1000 * 60 * 60 * 24 * 7,
                  };
                  localStorage.setItem('tokenTimer', JSON.stringify(obj));
                  // 限制
                  this.$store.commit('addToken', res.body.access_token);
                  localStorage.setItem('user', JSON.stringify(Res.body));
                  this.$router.push({ name: "AssistOrders", query: { rid: res.body.rid } });
                  // that.userChart(Res.body.rid);
                } else {
                  this.npLength = 'length0'//进度条
                  this.$message({
                    message: '验证码错误',
                    type: 'warning'
                  });
                }

              });
            }, function(err) {
              this.npLength = 'length0'//进度条
              if (err.body.error_description == '坏的凭证') {
                err.body.error_description = '账号或者密码错误'
              }
              this.$message({
                message: err.body.error_description,
                type: 'warning'
              });
            });
          } else {
            this.npLength = 'length100'//进度条
            // 菜单存储数据的去除
            localStorage.removeItem('twoIsShow');
            localStorage.removeItem('twoIsShow2');
            localStorage.removeItem('childTree');
            localStorage.removeItem('systemManagement');
            var url = '/api/uaa/oauth/token';
            var user = {
              grant_type: 'password',
              username: that.user.username,
              password: that.user.password,
              client_secret: '20170101',
              client_id: 'PC'
            }

            that.$http.post(url, user, { emulateJSON: true }).then(function(res) {
              // 记住密码
              if (that.checked) {
                localStorage.setItem('username', that.user.username);
                localStorage.setItem('password', that.user.password);
                localStorage.setItem('checked', '11')
              }
              if (!that.checked) {
                localStorage.removeItem('username');
                localStorage.removeItem('password');
                localStorage.removeItem('checked')
              }
              // 把這個存起來，用於判斷用戶是否登陸
              localStorage.setItem('access_token', res.body.access_token);
              // sessionStorage.setItem('access_token', res.body.access_token);

              // 限制
              var obj = {
                nowTime: new Date().getTime(),
                timeLimit: 1000 * 60 * 60 * 24 * 7,
              };
              localStorage.setItem('tokenTimer', JSON.stringify(obj))
              // 限制
              this.$store.commit('addToken', res.body.access_token);
              localStorage.setItem('user', JSON.stringify(res.body));
              this.$router.push({ name: "AssistOrders", query: { rid: res.body.rid } });
              // that.userChart(res.body.rid);
            }, function(err) {
              this.npLength = 'length0'//精度条
              if (err.body.error_description == '坏的凭证') {
                err.body.error_description = '账号或者密码错误'
              }
              this.$message({
                message: err.body.error_description,
                type: 'warning'
              });
              that.count++
              if (that.count >= 3) {
                that.count = 0;
                that.VerificationCode = true;
                var url = 'api/uaa/eztUsers/randomCode'
                that.$http.post(url).then(function(res) {
                  this.imgUrl = res.bodyText
                })
              }
            });
          }
        } else {
          return false
        }
      })

    },
    longByHx: function(name, password) {
      var options = {
        apiUrl: WebIM.config.apiURL,
        user: name,
        pwd: password,
        appKey: WebIM.config.appkey
      };
      localStorage.setItem('easemob', JSON.stringify({ name: name, password: password }));
      conn.open(options);
    },
    //用户信息
    userChart: function(rid) {
      var url = 'api/uaa/eztUsers/profile';
      var token = localStorage.getItem('access_token');
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + token
        }
      }).then(function(res) {
        res = res.body;
        var easemobUserName = res.username;
        var easemobUserPassword = res.password;
        if (res.type != 1 && res.type != 2) {
          this.$router.push({ name: "Expert", query: { rid: rid } });
        } else {
          this.longByHx(easemobUserName, easemobUserPassword);;
          this.$store.commit('easemobPost', { username: easemobUserName, password: easemobUserPassword });
          this.$router.push({ name: "Expert", query: { rid: rid } });
        }
      })
    },
    // 忘记密码
    changePassword() {
      this.$router.push({ name: 'FillCode' })
    },
    // 获取验证码
    getCodeUrl() {
      var url = 'api/uaa/eztUsers/randomCode'
      this.$http.post(url).then(function(res) {
        this.imgUrl = res.bodyText
      })
    }
  },
  components: {
    nprogress: nprogress
  }
}


</script>


<style scoped>
.dex {
  min-width: 1200px;
}

.header {
  height: 60px
}

.header .header-left {
  margin-left: 20px;
  float: left;
  font-size: 27px;
  font-weight: 700
}

.header .header-right {
  margin-right: 20px;
  float: right;
}
























/*.dex{width:100%;height:100%;background:#6A84D9;}*/

.header-right-textOne {
  margin-right: 59px;
  color: #333333;
  font-weight: 700;
  font-size: 18px;
  height: 60px;
  line-height: 60px;
}

.header-right-textTwo {
  color: #333333;
  font-size: 18px;
  height: 60px;
  line-height: 60px;
  font-weight: 700;
}

.center {
  position: relative;
  width: 100%;
  height: 760px;
  background: url('../assets/25newBack.png') no-repeat center center;
}


.tu {
  width: 1200px;
  margin: 0 auto;
  height: 760px;
  padding-top: 100px;
}

.menu {
  width: 460px;
  height: 510px;
  background: white;
  border: 10px #788ED7 solid;
  padding: 38px 60px 104px 60px;
  margin-top: 60px;
}

.tu h2 {
  height: 63px;
  border-bottom: solid 1px #5D7ADC;
}

.VerificationCode a {
  display: inline-block;
  width: 110px;
  height: 38px;
  background: #E6E6E6;
  float: right;
}

.VerificationCode a img {
  width: 100%;
  height: 100%;
}


.remenberWord {
  margin-top: 4px;
  font-size: 18px;
}

.remenberWord .remenberWord-L {
  font-weight: 700;
}

.remenberWord .remenberWord-R {
  font-weight: 700;
  color: #5D7ADC;
  cursor: pointer;
}

.logn-from button {
  width: 320px;
  height: 46px;
  background: #5D7ADC;
  border: none;
  color: white;
  font-size: 18px;
  margin-top: 38px;
}









/*底部样式*/

.footerOne {
  background: #E8ECF7;
  height: 130px;
  color: #8D8F92;
  font-size: 15px;
  line-height: 130px;
  width: 100%;
  text-align: center;
}

.footerOne .footer-text {
  width: 100%;
  height: 100%
}




/*进度条过度*/
</style>



