<template>
  <div :class='nprogressEvent' class='nprogress'>
    
  </div>
</template>
<script>  
export default {
  data(){
    return {
    }
  },
  props:['nprogressEvent']

}
</script>

<style>
.nprogress{
  position:fixed;
  left:0;
  top:0;
  width:100%;
  height:3px;
  background:#20a0ff;
  transition:all 2s;
}
.length0{
  width:0;
}
.length30{
  width:30%;
}
.length60{
  width:60%
}
.length100{
  width:100%;
}
  
</style>


